.main_span{
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.p_main_span1{
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: .1rem;
    font-weight: 400;
    color: #4a4a56;
}
.p_main_span2{
    font-size: 4.50839727rem;
    font-weight: 700;
    line-height: 1.05;
    letter-spacing: -.03em;
    margin-top: 0;
}
.result_div{
    //width: 80%;
    display: flex;
    justify-content: center;
    line-height: 1.2;
    letter-spacing: -.03em;
    align-items:center;
}
.result_info{
    margin: 1rem;
    display:flex;
    flex-direction: column;
    align-items:center;
}
.result_number{
    width: 6rem;
    height: 6rem;
    border-radius: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:white;
    font-weight: 700;
    font-size: 2.587464rem;
    margin-bottom: 10px;
}
.result_text{
    margin-top: 0;
}
.result_timer{
    margin: 1rem;
    width: 110px;
    height: 110px;
}

@media (max-width: 800px) {
  .main_span {
    margin-top: 1rem;
  }

  .p_main_span1 {
    font-size: 0.7rem;
  }

  .p_main_span2 {
    font-size: 2.3rem;
  }
  .result_info{
    margin: 0.4rem;
  }
  .result_number {
    width: 4rem;
    height: 4rem;
    font-size: 1.2rem;
  }
  .result_div{
    align-items: normal;
    justify-content: normal;
  }
  .result_timer {
    width: 80px;
    height: 70px;
  }
}
